.row{
    margin-left: 20px;
    color:white;
}
.row-posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
    &::-webkit-scrollbar{
        display: none;
    }

    .row-poster{
        object-fit: contain;
        width: 100%;
        max-height: 100px;
        transition: transform 450ms;
        margin-right: 10px;
        &:hover{
            transform: scale(1.08);
            
        }
    
    }
    .row-poster-large{
        max-height: 250px;
        &:hover{
            transform: scale(1.09);
        }
    }
}
